html {
  font-size: 10px;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  width: 100%;
}
*,
*::before,
*::after {
  box-sizing: inherit;
  font-family: inherit;
  outline: none;
}
#root {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  width: 340px;
  height: 90vh;
}
.container > div {
  padding: 0 20px;
}
.material-icons {
  cursor: pointer;
}
.df {
  display: flex;
}
.df.aic {
  align-items: center;
}
.df.jcc {
  justify-content: center;
}
.df.jcsa {
  justify-content: space-around;
}
.mb-1 {
  margin-bottom: 1rem;
}
.button {
  flex: 1;
}
.button,
.button:link,
.button:visited {
  border: none;
  background-color: #26a7a4;
  color: #fff;
  font-weight: 700;
  transition: all 0.3s ease;
  will-change: box-shadow;
  box-shadow: 0 0.3rem 0.1rem -0.2rem rgba(0, 0, 0, 0.2), 0 0.2rem rgba(0, 0, 0, 0.14), 0 0.1rem 0.5rem 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  text-transform: capitalize;
  font-size: 1.4rem;
  vertical-align: middle;
  user-select: none;
  padding: 0 1.6rem;
  height: 3.6rem;
  min-width: 8.8rem;
  border-radius: 0.2rem;
}
.button:hover {
  transform: translateY(-0.2rem);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.button:active,
.button:focus {
  transform: translateY(-0.1rem);
  box-shadow: 0 0.5rem 0.5rem -0.3rem rgba(0, 0, 0, 0.2), 0 0.8rem 1rem 0.1rem rgba(0, 0, 0, 0.14), 0 0.3rem 1.4rem 0.2rem rgba(0, 0, 0, 0.12);
}
.button--circle {
  border-radius: 50%;
  padding: 1rem;
  min-width: auto;
  height: auto;
}
.update-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
}
.table {
  width: 100%;
  border-radius: 0.5rem;
  border-collapse: collapse;
  overflow: hidden;
  table-layout: fixed;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
}
.table tr {
  height: 6rem;
  font-size: 1.3rem;
}
.table tr:not(:last-child) {
  border-bottom: 0.6px solid rgba(138, 151, 166, 0.3);
}
.table thead {
  color: #ffffff;
  background-color: #BC0022;
}
.table td {
  padding: 1rem 0;
  vertical-align: middle;
  text-align: center;
}
.table__header {
  color: #ffffff;
  background-color: #BC0022;
  height: 6rem;
  font-size: 1.3rem;
}
.table__wrapper {
  height: 80vh;
  overflow-y: auto;
}
input {
  font-weight: 300;
  font-size: 1.3rem;
  border: 1px solid #DCDFE6;
  border-radius: 0.4rem;
  width: 100%;
  color: #606266;
  padding: 0 1rem;
  height: 4rem;
  flex: 1;
}
